/* eslint-disable no-await-in-loop, quote-props */
import NovelFetchError from './NovelFetchError';

const pickups = [
  '4157ac86',
  'aaaafbfe'
];
const novels: KeyedStorage<{(): Promise<NovelMeta>}> = {
  '4157ac86': async () => import('./novel_data/.novels/4157ac86/meta.json') as any as Promise<NovelMeta>,
  '21481650': async () => import('./novel_data/.novels/21481650/meta.json') as any as Promise<NovelMeta>,
  aaaafbfe: async () => import('./novel_data/.novels/aaaafbfe/meta.json') as any as Promise<NovelMeta>,
};
const episodes: KeyedStorage<{(): Promise<Episode>}[]> = {
  '4157ac86': [
    async () => import('./novel_data/.novels/4157ac86/episode_0001.json') as any as Promise<Episode>,
    async () => import('./novel_data/.novels/4157ac86/episode_0002.json') as any as Promise<Episode>,
    async () => import('./novel_data/.novels/4157ac86/episode_0003.json') as any as Promise<Episode>,
    async () => import('./novel_data/.novels/4157ac86/episode_0004.json') as any as Promise<Episode>,
    async () => import('./novel_data/.novels/4157ac86/episode_0005.json') as any as Promise<Episode>,
    async () => import('./novel_data/.novels/4157ac86/episode_0006.json') as any as Promise<Episode>,
    async () => import('./novel_data/.novels/4157ac86/episode_0007.json') as any as Promise<Episode>,
  ],
  '21481650': [
    async () => import('./novel_data/.novels/21481650/episode_0001.json') as any as Promise<Episode>,
    async () => import('./novel_data/.novels/21481650/episode_0002.json') as any as Promise<Episode>,
    async () => import('./novel_data/.novels/21481650/episode_0003.json') as any as Promise<Episode>,
    async () => import('./novel_data/.novels/21481650/episode_0004.json') as any as Promise<Episode>,
  ],
  aaaafbfe: [
    async () => import('./novel_data/.novels/aaaafbfe/episode_0001.json') as any as Promise<Episode>,
    async () => import('./novel_data/.novels/aaaafbfe/episode_0002.json') as any as Promise<Episode>,
    async () => import('./novel_data/.novels/aaaafbfe/episode_0003.json') as any as Promise<Episode>,
  ],
};

export default {
  async fetchNovel(novel_id: string): Promise<NovelMeta> {
    const novel = novels[novel_id];
    if (!novel) {
      throw new NovelFetchError('novel_not_found');
    }
    return (await novel() as any).default;
  },
  async fetchEpisode(novel_id: string, episode_id: string): Promise<FetchedEpisodeData> {
    const novel = novels[novel_id];
    if (!novel) {
      throw new NovelFetchError('novel_not_found');
    }
    const index = (episode_id as any as number) * 1;
    const episode = episodes[novel_id][index];
    if (!episode) {
      throw new NovelFetchError('episode_not_found');
    }
    const { meta, lines } = (await episode() as any).default;
    console.log(lines.some((it: any) => !it));
    return {
      meta: (await novel() as any).default,
      episode: {
        lines,
        meta: {
          novel_id,
          has_next: episodes[novel_id].length > (index + 1),
          ...meta,
        },
      },
    };
  },
  async fetchEpisodeList(novel_id: string): Promise<EpisodeMeta[]> {
    const eps = episodes[novel_id];
    const ret = [];
    if (!eps) {
      throw new NovelFetchError('novel_not_found');
    }
    for (let idx = 0; idx < eps.length; idx++) {
      const it = eps[idx];
      const { meta } = (await it() as any).default;
      ret.push({ novel_id, has_next: idx !== eps.length - 1, ...meta });
    }
    return ret;
  },
  async searchNovel(query: string): Promise<KeyedStorage<NovelMeta>> {
    let ns = await Promise.all(
      Object
        .keys(novels)
        .filter((it) => novels.hasOwnProperty(it))
        .map(async (it) => ({ novel: await novels[it](), id: it })));
    if (query === 'filter:pickup') {
      ns = ns.filter((it) => pickups.findIndex((mt) => mt === it.id) !== -1);
    }
    return ns.reduce((prev, it) => ({ [it.id]: it.novel, ...prev }), {});
  },
};
