/* eslint-disable no-unused-vars */
import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import fetcher from '../fetcherimpl';
import NovelFetchError from '../fetcherimpl/NovelFetchError';

interface Props {
  novel?: string;
}
interface State {
  list?: EpisodeMeta[];
  novel?: NovelMeta;
  error?: FetchErrorType;
}

class NovelIndex extends Component<Props, State> {
  constructor(props: Props) {
    super(props);

    this.state = {};
  }

  componentDidMount() {
    if (!this.props.novel) {
      this.setState({
        error: {
          type: 'error',
          phase: 'precondition',
          detail: 'URLが不正です。',
        },
      });
      return;
    }
    fetcher
      .fetchEpisodeList(this.props.novel)
      .then((list) => {
        this.setState({ list });
      })
      .catch((err: NovelFetchError) => {
        this.setState({ error: err.source });
      });
    fetcher
      .fetchNovel(this.props.novel)
      .then((novel) => {
        this.setState({ novel });
      })
      .catch((err: NovelFetchError) => {
        this.setState({ error: err.source });
      });
  }

  render() {
    if (this.state.error) {
      switch (this.state.error) {
      case 'novel_not_found':
        return (
          <div className="reader">
            <div className="modal">
              <span className="modal-content_single">
                小説が見つかりません。削除済みか、URLが間違っています。
              </span>
            </div>
          </div>
        );
      case 'episode_not_found':
        return (
          <div className="reader">
            <div className="modal">
              <span className="modal-content_single">
                指定された小説の部分が見つかりません。削除済みか未投稿、またはURLが間違っています。
              </span>
            </div>
          </div>
        );
      default:
        return (
          <div className="reader">
            <div className="modal">
              <span className="modal-content_single">
                技術的な問題が発生しました。お手数ですが、エラーメッセージを添えてお問い合わせください。
                <span className="error-details">{this.state.error.detail}</span>
              </span>
            </div>
          </div>
        );
      }
    }
    if (!this.state.novel || !this.state.list) {
      return (
        <div className="novel-index">
          <div className="spinner" />
        </div>
      );
    }
    console.log(this.state.list);
    return (
      <div className="novel-index">
        <div className="episode-list-container">
          <div className="novel-details">
            <h1 className="novel-title">{this.state.novel.title}</h1>
            <div className="novel-author">{this.state.novel.author}</div>
            <div className="novel-summary-container">
              <div className="novel-thumbnail">
                <img src={this.state.novel.binding} alt={this.state.novel.title} />
              </div>
              <div className="novel-summary">{this.state.novel.summary}</div>
            </div>
          </div>
          <h2>エピソード</h2>
          <ol className="episode-list">
            {
              this
                .state
                .list
                .map((it, idx) => (
                  <li>
                    <Link to={`/view/${this.props.novel}/${idx}`}>{it.sub_title}</Link>
                  </li>
                ))
            }
          </ol>
        </div>
      </div>
    );
  }
}

export default NovelIndex;
