import React, { FC } from 'react';
import NovelIndex from '../NovelIndex';

const getEpisodeId = (): string | undefined => {
  return ((window.location.hash.match(/^#\/view\/(?<nid>[a-zA-Z0-9]{8})/) || {}).groups || {}).nid;
};

const IndexPage: FC = () => (
  <NovelIndex novel={getEpisodeId()} />
);

export default IndexPage;
